main {
  overflow: auto;

  h1,
  h2 {
    clear: both;
  }
}

figure {
  display: inline-block;
  padding: 0 auto;

  img {
    border: none;
  }

  figcaption {
    text-align: center;
    color: darkgray;
  }
}

.float-left,
.float-right {
  max-width: 40%;
}

.float-left {
  float: left;
}
.float-right {
  float: right;
}

pre code {
  max-height: 50vh;
  overflow-y: scroll;
}
